import * as React from "react"

import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "./join-us.css"

const join_us = () => (
    <Layout page="join_us">
      <Seo title="Join Us" description="Become CIS Volunteers | Please fill out the contact information form. We will reach out to you within a week. We will schedule an interview with you. Attend our new member orientation." />
      <section className="section">
        <div className="widthLimit-join-us">
          <div className="columns">
            <div className="column is-two-fifths">
              <div className="join-us-innerTextContainer">
                <strong className="join-us-title">Become CIS Volunteers</strong>
                <strong className="join-us-h2">Steps to join CIS</strong>
                <div className="join-us-step">
                  <li>Please fill out the contact information form</li>
                  <li>We will reach out to you within a week</li>
                  <li>We will schedule an interview with you</li>
                  <li>Attend our new member orientation</li>
                </div>
              </div>
            </div>
            <div className="column">
              <iframe
                src="https://docs.google.com/forms/d/e/1FAIpQLScWojobK5T4qggdzODzspM1g-8rBJngt8ANNel9GbjLu9UZ2w/viewform?embedded=true"
                title="Send Us a Message"
                width="100%"
                height="498"
                style={{
                  paddingTop: "24px"
                }}
              >Loadingâ€¦
              </iframe>
            </div>
          </div>
        </div>
      </section>
      <section className="join-us-title-div">
        <div className="widthLimit-join-us">
          <strong className="title2-join-us">Join the CIS Management Team</strong>
        </div>
      </section>
      <section className="section">
        <div className="widthLimit-join-us">
            <div className="join-us-step">
              <li>As a startup non-profit organization, we are looking for more passionate people to join our team.</li>
              <li>If you would like to become our project leader, staff, or board member, please email volunteer@sfcis.org with your resume and cover letter (optional). Please feel free to get in touch with us if you have any questions or concerns. Thanks for your interest and we will get back to you soon.</li>
            </div>
        </div>
      </section>
    </Layout>
)

export default join_us
